import { IVacancy } from '../types';

export const mapToVacancies = (response: any[]):Vacancy[] => response.map((vacanciesData) =>
  new Vacancy(
      vacanciesData.id || null,
      vacanciesData.title || null,
      vacanciesData.salary_extra || null,
      vacanciesData.salary_minimum || null,
      vacanciesData.salary_maximum || null,
      vacanciesData.jobTypeFlag || null,
      vacanciesData.jobTypeImage || null,
      vacanciesData.job_location || null,
      vacanciesData.jobLocationFlag || null,
      vacanciesData.content || null,
      vacanciesData.contract_details || null,
      vacanciesData.package_benefits || null,
      vacanciesData.who_can_apply || null,
      vacanciesData.equirements_details || null,
      vacanciesData.job_details || null,
      vacanciesData.expenses_details || null,
  )
);

class Vacancy implements IVacancy {
  constructor(
    public id: number,
    public title: string,
    public salaryExtra: string,
    public salaryMinimum: string,
    public salaryMaximum: string,
    public jobTypeFlag: string,
    public jobTypeImage: string,
    public jobLocation: string,
    public jobLocationFlag: string,
    public jobDescription: string,
    public jobContract: string,
    public jobPackageBenefits: string,
    public jobWhoCanApply: string,
    public jobRequirements: string,
    public jobDetails: string,
    public jobExpensesDetails: string,
  ) { }
}
