import { createStackNavigator } from '@react-navigation/stack';
import * as React from 'react';
import { JobTypesStackParamList } from '../types';
import JobTypesList from '../screens/JobTypesList';
import JobTypeList from '../screens/JobTypeList';
import JobLocation from '../screens/JobLocation';

export default function Navigation() {
  return (
      <JobTypesNavigator/>
  );
}

const Stack = createStackNavigator<JobTypesStackParamList>();

function JobTypesNavigator() {
  return (
    <Stack.Navigator screenOptions={{ headerShown: false }} initialRouteName="JobTypesList">
      <Stack.Screen name="JobTypesList" component={JobTypesList} />
      <Stack.Screen name="JobTypeList" component={JobTypeList} />
      <Stack.Screen name="JobLocation" component={JobLocation} />
    </Stack.Navigator>
  );
}
