import { FaqAction, FaqState, SetFaqElementsAction } from '../types'
import { FAQ_ACTION_TYPES } from './actions'

const initialState: FaqState = [];

export const faq = (
  state: FaqState = initialState,
  action: FaqAction
): FaqState => {
  switch (action.type){
    case FAQ_ACTION_TYPES.SET_FAQ_ELEMENTS:
      return (<SetFaqElementsAction>action).payload;
    default:
      return [...state];
  }
}