import React, { useEffect } from 'react';
import { DrawerParamList } from '../types';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Left, Icon, Right, Button, Body, Content,Text } from "native-base";
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { AppState } from '../state/types';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { StyleSheet, ActivityIndicator } from 'react-native';
import Colors from '../constants/Colors';
import { API_TOKEN } from "../constants/Http";
import { setSignedIn } from '../state/auth/actions';

const Logout = ({
  navigation,
  onLogout
}: DrawerScreenProps<DrawerParamList, 'Logout'> & {
  onLogout: () => void
}) => {

  useEffect(() => {
    AsyncStorage.removeItem(API_TOKEN);
    AsyncStorage.removeItem('email');
    AsyncStorage.removeItem('pin');
    setTimeout(() => {
      onLogout();
    }, 2000); 
  }, []);

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.goBack()}
          >
            <Icon name="arrow-back" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>Logout</Text>
        </Body>
        <Right />
      </Header>
      <Content style={styles.content}>
        <ActivityIndicator size={50}/>
      </Content>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  auth: state.auth
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onLogout: () => {
    dispatch(setSignedIn(false));
  },
});

export default connect(mapStateToProps, mapDipatchToProps)(Logout)

const styles = StyleSheet.create({
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  applicationTitle: {
    color: Colors.action,
    fontWeight: 'bold',
    fontSize: 18
  },
  content: {
    flex: 1,
  }
});
