import React, { useEffect } from 'react';
import { NotificationsStackParamList } from '../types';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Left, Icon, Right, Button, Body, Content,Text, View } from "native-base";
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { AppState, INotification } from '../state/types';
import { setNotifications } from '../state/notifications/actions';
import { getApiClient } from '../service/ApiClient';
import { StyleSheet } from 'react-native';
import Colors from '../constants/Colors';

const Notification = ({
  navigation,
  route,
  notifications
}: DrawerScreenProps<NotificationsStackParamList, 'Notification'> & {
  notifications: INotification[]
}) => {

  const [notification, setNotification] = React.useState<INotification>();

  useEffect(() => {
    const notification = notifications.find((notification: INotification) => notification.id === route.params.notificationId);
    if (notification) {
      setNotification(notification);
      getApiClient().receiveNotification({id: notification.id}).catch(e => console.log(e))
    }
  }, []);

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.goBack()}
          >
            <Icon name="arrow-back" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>Notifications</Text>
        </Body>
        <Right />
      </Header>
      { notification && <Content>
        <View style={styles.notificationTitleContainer}>
          <Text numberOfLines={1} style={styles.notificationTitle}>{notification.title}</Text>
        </View>
        <Text style={styles.notificationBody}>{notification.body}</Text>
      </Content>}
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  user: state.user,
  notifications: state.notifications
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSetNotifications: (applcations: INotification[]) => {
    dispatch(setNotifications(applcations.reverse()));
  },
});

const styles = StyleSheet.create({
  notificationBody: {
    paddingVertical:  10,
    paddingHorizontal: 20
  },
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  notificationTitle: {
    color: Colors.action,
    fontWeight: 'bold',
    fontSize: 18
  },
  notificationTitleContainer: {
    flexDirection: 'column',
    paddingTop: 20,
    paddingHorizontal: 20,
    paddingBottom: 15,
    borderBottomWidth: 1,
    borderBottomColor: '#dadce0'
  }
});

export default connect(mapStateToProps, mapDipatchToProps)(Notification);
