import { IVacancy, SetVacanciesAction } from '../types'

export const VACANCIES_ACTION_TYPES = {
  SET_VACANCIES: 'VACANCIES/SET_VACANCIES',
  UPDATE_VACANCIES: 'VACANCIES/UPDATE_VACANCIES'
}

export const setVacancies = (vacancies: IVacancy[]): SetVacanciesAction => ({
  type: VACANCIES_ACTION_TYPES.SET_VACANCIES,
  payload: vacancies
});