import React, {useEffect} from 'react';
import { Dispatch } from 'redux';
import { connect } from 'react-redux';
import { ActivityIndicator, StyleSheet } from 'react-native';
import { Container, Header, Left, Icon, Right, Button, Body, Content, Text, View, Toast, H3 } from "native-base";
import { DrawerScreenProps } from '@react-navigation/drawer';
import { JobTypesStackParamList } from '../types';
import { AppState, IVacancy } from '../state/types';
import Colors from '../constants/Colors';
import JobTypeVacancy from '../components/job-type-vacancy';
import { getApiClient } from '../service/ApiClient';
import { UnauthorizedError } from '../errors/api/UnauthorizedError';
import { setVacancies } from '../state/vacancies/actions';

const JobTypeList = ({
  navigation,
  route,
  vacancies,
  onSetVacancies,
}: DrawerScreenProps<JobTypesStackParamList, 'JobTypeList'> & {
  vacancies: IVacancy[],
  onSetVacancies: (vacancies: IVacancy[]) => void,
}) => {
  const [loading, setLoading] = React.useState(false);

  useEffect(() => {
    setLoading(true);
    getApiClient().fetchVacancies({jobTypeId: route.params.jobType.id}).then(vacancies => {
      onSetVacancies(vacancies);
      setLoading(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      displayErrorMessage('Could not connect to Server. Try again later.');
      setLoading(false);
    });
  }, []);

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 5000,
      position: "top"
    })
  }

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.goBack()}
          >
            <Icon name="arrow-back" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>Jobs by location</Text>
        </Body>
        <Right />
      </Header>
      <Content>
        { loading && <ActivityIndicator size={50} />}
        { !loading && vacancies.length > 0
          ? (
                <View style={styles.jobData}>
                  <H3>{vacancies.length} results found</H3>
                  {vacancies.map(vacancy => (
                      <JobTypeVacancy key={vacancy.id} vacancy={vacancy} onPress={() => {
                        navigation.navigate('JobLocation', { vacancy });
                      }}/>
                  ))}
                </View>
            ) : null
        }
      </Content>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  user: state.user,
  vacancies: state.vacancies
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
  onSetVacancies: (vacancies: IVacancy[]) => {
    dispatch(setVacancies(vacancies));
  },
});

const styles = StyleSheet.create({
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  jobData: {
    flexDirection: 'column',
    paddingVertical: 14,
    paddingHorizontal: 20,
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(JobTypeList);
