import { INotification } from "../types";

export const mapToNotifications = (response: any[]):INotification[] => {
  return response.map((data) =>
    new Notification(
      data.id || null,
      data.title ?? '',
      data.body ?? '',
      data.type ?? '',
    )
  );
}

class Notification implements INotification {
  constructor(
    public id: number,
    public title: string,
    public body: string,
    public type: string,
  ) { }
}
