import { IFaqElement, SetFaqElementsAction } from '../types'

export const FAQ_ACTION_TYPES = {
  SET_FAQ_ELEMENTS: 'FAQ/SET_FAQ_ELEMENTS',
}

export const setFaqElements = (faqElements: IFaqElement[]): SetFaqElementsAction => ({
  type: FAQ_ACTION_TYPES.SET_FAQ_ELEMENTS,
  payload: faqElements
});
 