import { IJobType, SetJobTypesAction } from '../types'

export const JOBTYPES_ACTION_TYPES = {
  SET_JOBTYPES: 'JOBTYPES/SET_JOBTYPES',
  UPDATE_JOBTYPES: 'JOBTYPES/UPDATE_JOBTYPES'
}

export const setJobTypes = (jobTypes: IJobType[]): SetJobTypesAction => ({
  type: JOBTYPES_ACTION_TYPES.SET_JOBTYPES,
  payload: jobTypes
});