import { createDrawerNavigator } from '@react-navigation/drawer';
import * as React from 'react';
import { DrawerParamList } from '../types';
import Logout from '../screens/Logout';
import JobNavigation from './JobNavigator';
import JobTypesNavigation from './JobTypesNavigator';
import NotificationsNavigation from './NotificationsNavigator';
import { Icon } from 'native-base';
import Colors from '../constants/Colors';
import Faq from '../screens/Faq';

export default function DrawerNavigation() {
  return (
    <DrawerNavigator/>
  );
}

const Drawer = createDrawerNavigator<DrawerParamList>();

function DrawerNavigator() {
  return (
    <Drawer.Navigator screenOptions={{ headerShown: false }}>
      <Drawer.Screen
        name="JobNavigation"
        component={JobNavigation}
        options={{ 
          drawerLabel: 'MY APPLICATIONS', 
          drawerIcon: () => <Icon type='FontAwesome' name='hourglass-o' style={{fontSize: 20, color: Colors.main}}/> 
        }}
      />
      <Drawer.Screen
        name="JobTypesNavigation"
        component={JobTypesNavigation}
        options={{
            drawerLabel: 'JOBS BY LOCATION',
            drawerIcon: () => <Icon type='FontAwesome' name='location-arrow' style={{fontSize: 20, color: Colors.main}}/>
        }}
      />
      <Drawer.Screen
        name="NotificationsNavigation"
        component={NotificationsNavigation}
        options={{ 
          drawerLabel: 'NOTIFICATIONS', 
          drawerIcon: () => <Icon type='MaterialCommunityIcons' name='message-processing-outline' style={{fontSize: 20, color: Colors.main}}/> 
        }}
      />
      <Drawer.Screen
        name="Faq"
        component={Faq}
        options={{ 
          drawerLabel: 'FAQ', 
          drawerIcon: () => <Icon type='MaterialCommunityIcons' name='information-outline' style={{fontSize: 20, color: Colors.main}}/> 
        }}
      />
      <Drawer.Screen
        name="Logout"
        component={Logout}
        options={{ 
          drawerLabel: 'LOGOUT', 
          drawerIcon: () => <Icon type='MaterialCommunityIcons' name='logout' style={{fontSize: 20, color: Colors.main}}/> 
        }}
      />
    </Drawer.Navigator>
  );
}
