import { SetJobTypesAction, JobTypesAction , JobTypesState } from '../types'
import { JOBTYPES_ACTION_TYPES } from './actions'

const initialState: JobTypesState = [];

export const jobTypes = (
  state: JobTypesState = initialState,
  action: JobTypesAction
): JobTypesState => {
  switch (action.type){
    case JOBTYPES_ACTION_TYPES.SET_JOBTYPES:
      return (<SetJobTypesAction>action).payload;
    default:
      return [...state];
  }
}