import React, { useEffect, useState } from 'react';
import { DrawerParamList } from '../types';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Left, Icon, Right, Button, Body, Content,Text, View, Toast, Accordion } from "native-base";
import { connect, Dispatch } from 'react-redux';
import { AppState, IApplication, IFaqElement } from '../state/types';
import { setFaqElements } from '../state/faqElements/actions';
import { getApiClient } from '../service/ApiClient';
import { ActivityIndicator, StyleSheet, useWindowDimensions } from 'react-native';
import Colors from '../constants/Colors';
import HTML from "react-native-render-html";
import { UnauthorizedError } from '../errors/api/UnauthorizedError';
import { useFocusEffect } from '@react-navigation/native';

const Faq = ({navigation, faqElements, onSetFaqElements}: DrawerScreenProps<DrawerParamList, "Faq">) => {

const [loading, setLoading] = useState(false);


  const loadData = () => {
    setLoading(true);
    getApiClient().fetchFaq().then(faqElements => {
      onSetFaqElements(faqElements);
      setLoading(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      displayErrorMessage('Could not connect to Server. Try again later.');
      setLoading(false);
    });
  }

  useFocusEffect(
    React.useCallback(() => {
      loadData();
    }, [])
  );

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 5000,
      position: "top"
    })
  }
  
  const contentWidth = useWindowDimensions().width;
  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.goBack()}
          >
            <Icon name="arrow-back" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>FAQ</Text>
        </Body>
        <Right />
      </Header>
      {!loading ? <Content padder>
        <Accordion dataArray={faqElements.map((faqElement:IFaqElement) => {
          return {
            title: <View style={styles.faqElementTitle}>
                    <Text style={styles.faqElementTitleText}>{faqElement.title}</Text>
                  </View>,
            content: <View style={styles.faqElementContent}>
                      {/* <Text style={styles.faqElementContentText}>{faqElement.content}</Text> */}
                      <HTML source={{html: faqElement.content}} contentWidth={contentWidth}/>
                    </View>
          }
        })} 
        expanded={0}
        headerStyle={{borderBottomWidth: 1, borderBottomColor: '#d7d7d7', backgroundColor: '#edebed'}}
        />
      </Content>
      : 
        <ActivityIndicator size={50}/>
      }
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  faqElements: state.faq
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSetFaqElements: (faqElements: IFaqElement[]) => {
    dispatch(setFaqElements(faqElements));
  },
});

const styles = StyleSheet.create({
  faqElementContent: {
    paddingVertical: 10,
    paddingHorizontal: 10,
  },
  faqElementContentText: {

  },
  faqElementTitle: {
    paddingVertical: 5,
    flexDirection: 'row',
  },
  faqElementTitleText: {
    fontWeight: 'bold',
  },
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  }
});

export default connect(mapStateToProps, mapDipatchToProps)(Faq);
