import React, { useEffect } from 'react';
import { JobStackParamList } from '../types';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Left, Icon, Right, Button, Body, Content,Text, View } from "native-base";
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { AppState, IApplication } from '../state/types';
import { setApplications } from '../state/applications/actions';
import { getApiClient } from '../service/ApiClient';
import { RefreshControl, StyleSheet, useWindowDimensions } from 'react-native';
import Colors from '../constants/Colors';
import Status from '../components/Status';
import { UnauthorizedError } from '../errors/api/UnauthorizedError';
import HTML from "react-native-render-html";
import { useFocusEffect } from '@react-navigation/native';

const Job = ({
  navigation,
  route,
  applications,
  onSetApplications
}: DrawerScreenProps<JobStackParamList, 'Job'> & {
  applications: IApplication[],
  onSetApplications: (applications: IApplication[]) => void
}) => {

  const [refreshing, setRefreshing] = React.useState(false);
  const [application, setApplication] = React.useState<IApplication>();
  const [recruiterComment, setRecruiterComment] = React.useState<string | null>(null);
  const [statusComment, setStatusComment] = React.useState<string | null>(null);

  const onRefresh = () => {
    setRefreshing(true);
    getApiClient().fetchApplications().then(applications => {
      onSetApplications(applications);
      setApplication(applications.find((application: IApplication) => application.id === route.params.applicationId));
      application && processComments(application);
      setRefreshing(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      setRefreshing(false);
    });
  }

  useFocusEffect(
    React.useCallback(() => {
      onRefresh();
    }, [])
  );

  const processComments = (application: IApplication) => {
    let recruiterComment = application.comment;
    if (application?.visaApplication?.comment) {
      recruiterComment = application.visaApplication.comment;
    }
    setRecruiterComment(recruiterComment);

    let statusComment = application.statusComment;
    if (application?.visaApplication?.statusComment) {
      statusComment = application.visaApplication.statusComment;
    }
    setStatusComment(statusComment);
  }

  useEffect(() => {
    const application = applications.find((application: IApplication) => application.id === route.params.applicationId);
    setApplication(application);
    application && processComments(application);
  }, []);

  const contentWidth = useWindowDimensions().width;

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.goBack()}
          >
            <Icon name="arrow-back" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>My Applications</Text>
        </Body>
        <Right />
      </Header>
      { application && <Content
        refreshControl={
          <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
        }
      >
        <View style={styles.jobData}>
          <Text numberOfLines={1} style={styles.applicationTitle}>{application.jobName}</Text>
          <Status style={{paddingTop: 10}} application={application.visaApplication || application}>
            {application.visaApplication ? application.visaApplication.status : application.status}
          </Status>
        </View>
        { statusComment && <View style={styles.commentContainer}> 
            <Text style={styles.recruiterCommentHeaderText}>
              <Icon type="AntDesign" name='message1' style={{fontSize: 15}}/>  Status Comment
            </Text>
            <HTML source={{html: statusComment}} contentWidth={contentWidth}/>
        </View>
        }
        { recruiterComment && <View style={styles.commentContainer}> 
            <Text style={styles.recruiterCommentHeaderText}>
              <Icon type="AntDesign" name='message1' style={{fontSize: 15}}/>  Recruiter Comment
            </Text>
            <HTML source={{html: recruiterComment}} contentWidth={contentWidth}/>
        </View>
        }
      </Content>}
      <View style={styles.refreshContentInformation}>
        <Text style={styles.refreshContentInformationText}>Pull down to refresh</Text>
      </View>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  applications: state.applications
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSetApplications: (applications: IApplication[]) => {
    dispatch(setApplications(applications.reverse()));
  },
});

const styles = StyleSheet.create({
  refreshContentInformation: {
    height: 60,
    textAlign: 'center',
    width:  '100%',
    paddingTop: 10,
    alignItems: 'center',
  },
  refreshContentInformationText: {
    color: '#ada4a4'
  },
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  applicationTitle: {
    color: Colors.action,
    fontWeight: 'bold',
    fontSize: 18
  },
  commentContainer: {
    flex: 1,
    paddingVertical: 20,
    paddingHorizontal: 20,
  },
  recruiterCommentHeaderText: {
    fontWeight: 'bold',
    textAlign: 'left',
    paddingBottom: 10
  },
  jobData: {
    flexDirection: 'column',
    paddingTop: 10,
    paddingHorizontal: 20,
    paddingBottom: 15,
    borderBottomWidth: 2,
    borderBottomColor: '#dadce0'
  },
  listButtonContainer:{
    flex: 0.15
  }
});

export default connect(mapStateToProps, mapDipatchToProps)(Job);
