import Constants from 'expo-constants';
import * as Notifications from 'expo-notifications';
import React, { useState, useEffect, useRef } from 'react';
import { View, Platform } from 'react-native';
import { Alert } from 'react-native';

Notifications.setNotificationHandler({
  handleNotification: async () => ({
    shouldShowAlert: true,
    shouldPlaySound: false,
    shouldSetBadge: true,
  }),
});

export default function NotificationComponent({
  onExpoPushTokenReceived = (expoPushToken) =>  {},
  onNotificationReceived = (notification) =>  {},
  onNotificationResponseReceived = (notificationResponse) =>  {},
  showAlertBeforePermissionsRequest = false,
  alertText = ''
}) {
  const notificationListener = useRef();
  const responseListener = useRef();

  useEffect(() => {
    registerForPushNotificationsAsync({
      showAlertBeforePermissionsRequest,
      alertText
    }).then(token => {
      onExpoPushTokenReceived(token);
    });

    notificationListener.current = Notifications.addNotificationReceivedListener(notification => {
      onNotificationReceived(notification);
    });

    responseListener.current = Notifications.addNotificationResponseReceivedListener(response => {
      onNotificationResponseReceived(response);
    });

    return () => {
      Notifications.removeNotificationSubscription(notificationListener);
      Notifications.removeNotificationSubscription(responseListener);
    };
  }, []);

  return (
    <View>
    </View>
  );
}


async function registerForPushNotificationsAsync({
  showAlertBeforePermissionsRequest,
  alertText = ''
}) {
  let token;
  if (Constants.isDevice && Platform.OS !== 'web') {
    const { status: existingStatus } = await Notifications.getPermissionsAsync();
    if (existingStatus !== 'granted') {
      if (showAlertBeforePermissionsRequest) {
        token = await (async () => new Promise((resolve) => {
          Alert.alert(
            "Notifications",
            alertText,
            [
              { 
                text: "OK", 
                onPress: async () => {
                  const { status } = await Notifications.requestPermissionsAsync();
                  if (status !== 'granted') {
                    alert('Failed to get push token for push notification!');
                    return;
                  }
                  resolve((await Notifications.getExpoPushTokenAsync()).data);
                }
              }
            ]
          );
        }))();
      } else {
        const { status } = await Notifications.requestPermissionsAsync();
        if (status !== 'granted') {
          alert('Failed to get push token for push notification!');
          return;
        }
        token = (await Notifications.getExpoPushTokenAsync()).data;
      }
    } else {
      token = (await Notifications.getExpoPushTokenAsync()).data;
    }

  } else {
    alert('Must use physical device for Push Notifications');
  }

  if (Platform.OS === 'android') {
    Notifications.setNotificationChannelAsync('default', {
      name: 'default',
      importance: Notifications.AndroidImportance.MAX,
      vibrationPattern: [0, 250, 250, 250],
      lightColor: '#FF231F7C',
    });
  }
  return token;
}
