import { SetVacanciesAction, VacanciesAction , VacanciesState } from '../types'
import { VACANCIES_ACTION_TYPES } from './actions'

const initialState: VacanciesState = [];

export const vacancies = (
  state: VacanciesState = initialState,
  action: VacanciesAction
): VacanciesState => {
  switch (action.type){
    case VACANCIES_ACTION_TYPES.SET_VACANCIES:
      return (<SetVacanciesAction>action).payload;
    default:
      return [...state];
  }
}