import { IJobType } from '../types';

export const mapToJobTypes = (response: any[]):JobType[] => response.map((jobTypeData) =>
  new JobType(
      jobTypeData.id || null,
      jobTypeData.name || null,
      jobTypeData.flag|| null,
      jobTypeData.image || null,
  )
);

class JobType implements IJobType {
  constructor(
    public id: number,
    public name: string,
    public flag: string,
    public image: string,
  ) { }
}
