import React, {useEffect} from 'react';
import { Dispatch } from "redux";
import { connect } from "react-redux";
import { ActivityIndicator, StyleSheet } from 'react-native';
import { Container, Header, Left, Icon, Right, Button, Body, Text, Content, List, ListItem, Toast } from 'native-base';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { JobTypesStackParamList } from '../types';
import { AppState, IJobType } from '../state/types';
import { setJobTypes } from '../state/jobTypes/actions';
import Colors from '../constants/Colors';
import { getApiClient } from '../service/ApiClient';
import { UnauthorizedError } from '../errors/api/UnauthorizedError';

const JobTypesList = ({ navigation,
  jobTypes,
  onSetJobTypes,
}: DrawerScreenProps<JobTypesStackParamList, 'JobTypesList'> & {
  jobTypes: IJobType[],
  onSetJobTypes: (jobTypes: IJobType[]) => void,
}) => {
  const [loading, setLoading] = React.useState(false);

  useEffect(() => {
    setLoading(true);
    getApiClient().fetchJobTypes().then(jobTypes => {
      onSetJobTypes(jobTypes);
      setLoading(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      displayErrorMessage('Could not connect to Server. Try again later.');
      setLoading(false);
    });
  }, []);

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 5000,
      position: "top"
    })
  }

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.openDrawer()}
          >
            <Icon name="menu" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>Jobs by location</Text>
        </Body>
        <Right />
      </Header>
      <Content>
        { loading && <ActivityIndicator size={50} />}
        { jobTypes.length > 0 ?
            <List>
              {jobTypes.map((jobType: IJobType) =>
                  <ListItem key={jobType.id}>
                    <Body>
                      <Text>{jobType.name}</Text>
                    </Body>
                    <Right>
                      <Button transparent onPress={() => {
                        navigation.navigate('JobTypeList', {jobType: jobType});
                      }}
                      >
                        <Icon name="arrow-forward"/>
                      </Button>
                    </Right>
                  </ListItem>
              )}
            </List> : null
        }
      </Content>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  user: state.user,
  jobTypes: state.jobTypes
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
  onSetJobTypes: (jobTypes: IJobType[]) => {
    dispatch(setJobTypes(jobTypes));
  },
});

const styles = StyleSheet.create({
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  icon: {
    width: 24,
    height: 24
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(JobTypesList);
