import React from 'react';
import { ImageBackground, StyleSheet } from 'react-native';
import { Card, CardItem, Text, View, Row, Grid, Left, Right, Button } from 'native-base';
import SvgUri from 'expo-svg-uri';
import Colors from '../../constants/Colors';
import { IVacancy} from '../../state/types';

interface JobTypeVacancyProps {
    vacancy: IVacancy,
    className?: string,
    onPress: () => void,
}

const JobTypeVacancy: React.FC<JobTypeVacancyProps> = ({ vacancy, onPress }) => {
    return (
        <Card>
            <CardItem cardBody>
                <View style={styles.imageContainer}>
                    <ImageBackground
                        source={{uri: vacancy.jobTypeImage}}
                        style={styles.imageBackground}
                        resizeMode="cover"
                    >
                        <View style={styles.imageOverlay} />
                        <Grid style={styles.hero}>
                            <Row size={3}>
                                <Left>
                                    <Text style={{color: Colors.lightText}}>{vacancy.title}</Text>
                                </Left>
                                <Right>
                                    <Text style={{color: Colors.lightText}}>${vacancy.salaryMinimum}  ${vacancy.salaryMaximum}</Text>
                                    {vacancy.salaryExtra && <Text style={{color: Colors.lightText, textAlign: 'center'}}>{vacancy.salaryExtra}</Text>}
                                </Right>
                            </Row>
                            <Row size={1}>
                                <Left>
                                    <SvgUri source={{uri: vacancy.jobLocationFlag}} width={20} height={20} />
                                    <Text style={{color: Colors.lightText, paddingVertical: 10}}>{vacancy.jobLocation}</Text>
                                </Left>
                            </Row>
                        </Grid>
                    </ImageBackground>
                </View>
            </CardItem>
            <CardItem>
                <View style={styles.actionsContainer}>
                    <SvgUri source={{uri: vacancy.jobTypeFlag}} width={20} height={20} />
                    <Grid>
                        <Row>
                            <Left>
                                <Text style={{fontSize: 12}}>For Taiwan Applicants</Text>
                            </Left>
                            <Right>
                                <Button transparent small onPress={onPress}>
                                    <Text>View more</Text>
                                </Button>
                            </Right>
                        </Row>
                    </Grid>
                </View>
            </CardItem>
        </Card>
    );
}

export default JobTypeVacancy;

const styles = StyleSheet.create({
    imageContainer: {
        height: 200,
        flex: 1,
    },
    imageOverlay: {
        height: "100%",
        width: "100%",
        position: 'absolute',
        backgroundColor: 'rgba(16, 63, 106, 0.6)',
    },
    imageBackground: {
        flex: 1,
    },
    actionsContainer: {
        paddingTop: 14,
        flex: 1,
    },
    hero: {
        paddingHorizontal: 17,
        paddingVertical: 10,
    }
})