import { StatusBar } from 'expo-status-bar';
import React from 'react';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import useCachedResources from './hooks/useCachedResources';
import Navigation, { navigationRef } from './navigation';
import AppLoading from 'expo-app-loading';
import store from './state/store';
import { Provider } from 'react-redux';
import { Root, StyleProvider } from "native-base";
import getTheme from './native-base-theme/components';
import platform from './native-base-theme/variables/platform';
import NotificationComponent from './core/notifications/NotificationComponent';
import { TYPE_BALANCE_REMINDER, TYPE_STATUS_CHANGE, TYPE_STATUS_NEW } from './constants/Notification';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { setNotifications } from './state/notifications/actions';
import { getApiClient } from './service/ApiClient';
import { setApplications } from './state/applications/actions';

export default function App() {
  const isLoadingComplete = useCachedResources();

  if (!isLoadingComplete) {
    return <AppLoading/>;
  } else {
    return (
      <Root>
        <Provider store={store}>
          <StyleProvider style={getTheme(platform)}>
            <SafeAreaProvider>
              <Navigation/>
            </SafeAreaProvider>
          </StyleProvider>
          <NotificationComponent
            onNotificationResponseReceived={(pushNotification) =>  {
              if (pushNotification?.notification?.request?.content?.data?.id) {
                getApiClient().receiveNotification({id: pushNotification?.notification?.request?.content?.data?.id})
                .catch(e => console.log(e));
              }
              switch (pushNotification?.notification?.request?.content?.data?.type) {
                case TYPE_STATUS_CHANGE:
                  navigationRef.current?.navigate("JobNavigation", {screen: "JobList"});
                  getApiClient().fetchApplications().then(applications => {
                    store.dispatch(setApplications(applications.reverse()));
                  });
                  break;
                case TYPE_STATUS_NEW:
                  navigationRef.current?.navigate("JobNavigation", {screen: "JobList"})
                  getApiClient().fetchApplications().then(applications => {
                    store.dispatch(setApplications(applications.reverse()));
                  });
                  break;
                case TYPE_BALANCE_REMINDER:
                  navigationRef.current?.navigate("NotificationsNavigation", {screen  :  "Notifications"});
                  getApiClient().fetchMessagesHistory({page: 1, perPage: 20}).then(notifications => {
                    store.dispatch(setNotifications(notifications));
                  });
                  break;
              }
            }}
            showAlertBeforePermissionsRequest={true}
            alertText="We will be sending you push notifications. This is important for you. Your phone will ask you if you allow Artellect app to do this. Please allow."
            onExpoPushTokenReceived={(expoPushToken: string | null) => {
                AsyncStorage.setItem('expoPushToken', String(expoPushToken))
              }
            }
          />
        </Provider>
      </Root>
    );
  }
}
