import { IFaqElement } from "../types";

export const mapToFaqElements = (response: any[]):IFaqElement[] => response.map((faqElementData) =>
  new FaqElement(
    faqElementData.id || null,
    faqElementData.title ?? '',
    faqElementData.body ?? '',
  )
);

class FaqElement implements IFaqElement {
  constructor(
    public id: number,
    public title: string,
    public content: string
  ) { }
}
