import { NotificationsAction, NotificationsState, SetNotificationsAction } from '../types'
import { NOTIFICATIONS_ACTION_TYPES } from './actions'

const initialState: NotificationsState = [];

export const notifications = (
  state: NotificationsState = initialState,
  action: NotificationsAction
): NotificationsState => {
  switch (action.type){
    case NOTIFICATIONS_ACTION_TYPES.SET_NOTIFICATIONS:
      return (<SetNotificationsAction>action).payload;
    default:
      return [...state];
  }
}