import React, { PureComponent, useRef, useState } from 'react';
import { NotificationsStackParamList } from '../types';
import { DrawerNavigationProp, DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Left, Icon, Right, Button, Body,Text, View, Toast, ListItem } from "native-base";
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { AppState, INotification } from '../state/types';
import { setNotifications } from '../state/notifications/actions';
import { getApiClient } from '../service/ApiClient';
import { ActivityIndicator, StyleSheet ,RefreshControl, useWindowDimensions, NativeScrollEvent, Platform } from 'react-native';
import Colors from '../constants/Colors';
import { UnauthorizedError } from '../errors/api/UnauthorizedError';
import { useFocusEffect } from '@react-navigation/native';
import { FlatList } from 'react-native';

const Notifications = ({
  navigation,
  notifications,
  onSetNotifications
}: DrawerScreenProps<NotificationsStackParamList, "Notifications"> & {
  notifications: INotification[],
  onSetNotifications: (notifications: INotification[]) => void}
) => {

  const [refreshing, setRefreshing] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [loadingMore, setLoadingMore] = useState<boolean>(false);
  const [loadedAllElements, setLoadedAllElements] = useState<boolean>(false);
  const [page, setPage] = useState<number>(1);
  const flatList = useRef<FlatList<INotification>>();

  const onRefresh = () => {
    setRefreshing(true);
    getApiClient().fetchMessagesHistory({page: 1, perPage: 20}).then(notifications => {
      onSetNotifications(notifications);
      setPage(1);
      setLoadedAllElements(false);
      setRefreshing(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      console.log(e);
      displayErrorMessage('Could not connect to Server. Try again later.');
      setRefreshing(false);
    });
  }

  const onFetchMore = () => {

    if (loadingMore || loadedAllElements){
      return;
    }
    setLoadingMore(true);

    getApiClient().fetchMessagesHistory({page: page + 1, perPage: 20}).then(_notifications => {
      if (_notifications.length) {
        onSetNotifications([...notifications,  ..._notifications]);
        setPage(page + 1);
      } else {
        setLoadedAllElements(true);
      }
      setLoadingMore(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      console.log(e);
      displayErrorMessage('Could not connect to Server. Try again later.');
      setLoadingMore(false);
    });
  }

  const isCloseToBottom = ({layoutMeasurement, contentOffset, contentSize}: NativeScrollEvent) => {
    const paddingToBottom = 20
    return layoutMeasurement.height + contentOffset.y >=
      contentSize.height - paddingToBottom
  }

  useFocusEffect(
    React.useCallback(() => {
      !refreshing && !loading && onRefresh();
    }, [])
  );

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 5000,
      position: "top"
    })
  }

  let height = useWindowDimensions().height;

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.openDrawer()}
          >
            <Icon name="menu" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>Notifications</Text>
        </Body>
        <Right />
      </Header>
      {loading && <ActivityIndicator size={50}/>}
        <FlatList
          ListFooterComponent={loadingMore ? <ActivityIndicator style={Platform.OS !== 'ios' ? {paddingTop: 10} : undefined} size={Platform.OS !== 'ios' ? 30 : 50} color={Platform.OS !== 'ios' ? '#ada4a4' : undefined}/> : undefined}
          ref={flatList}
          refreshControl={<RefreshControl refreshing={refreshing} onRefresh={onRefresh} />}
          data={notifications}
          keyExtractor={item => String(item.id  * Math.random())}
          onMomentumScrollEnd={({nativeEvent}) => 
            isCloseToBottom(nativeEvent) && !loadingMore && onFetchMore()
          }
          renderItem={({item, index}) => <NotoficationListItem notification={item} navigation={navigation}/> }
          ListEmptyComponent={!refreshing ? <View style={{padding: 5}}><Text style={{textAlign: 'center'}}>List is empty</Text></View> : undefined}
        >
        </FlatList>
      <View style={styles.refreshContentInformation}>
        <Text style={styles.refreshContentInformationText}>Pull down to refresh</Text>
      </View>
    </Container>
  );
}

class NotoficationListItem extends PureComponent<{notification: INotification, navigation: DrawerNavigationProp<NotificationsStackParamList, "Notifications">}> {
  render () {
    const item = this.props.notification;
    return <ListItem key={item.id * Math.random()} style={styles.listItem}>
      <Body style={styles.notificationTitle}>
        <Text numberOfLines={1} style={styles.notificationTitleText}>{item.title}</Text>
      </Body>
      <Right style={styles.listButtonContainer}>
        <Button transparent
          onPress={() => {
            this.props.navigation.navigate('Notification', {notificationId: item.id});
          }}
        >
          <Icon name="arrow-forward"/>
        </Button>
      </Right>
    </ListItem> 
  }
}


const mapStateToProps = (state: AppState) => ({
  notifications: state.notifications
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSetNotifications: (notifications: INotification[]) => {
    dispatch(setNotifications(notifications));
  },
});

const styles = StyleSheet.create({
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  notificationTitleText: {
    color: Colors.action,
    fontWeight: 'bold'
  },
  listItem: {
    marginLeft: 0,
    paddingLeft: 5
  },
  notificationTitle: {
    flexDirection: 'column',
  },
  listButtonContainer:{
    flex: 0.20,
  },
  refreshContentInformation: {
    height: 60,
    textAlign: 'center',
    width:  '100%',
    paddingTop: 10,
    alignItems: 'center',
  },
  loadingMoreAndroid: {
    padding: 10,
    textAlign: 'center',
    width:  '100%',
    paddingTop: 5,
    alignItems: 'center',
  },
  refreshContentInformationText: {
    color: '#ada4a4'
  },
});

export default connect(mapStateToProps, mapDipatchToProps)(Notifications);
