import axios, { AxiosInstance, AxiosResponse, AxiosRequestConfig } from "axios";
import config from '../config'
import { API_TOKEN } from "../constants/Http";
import { UndefinedApiTokenError } from "../errors/api/UndefinedApiTokenError";
import { ApiError } from "../errors/api/ApiError";
import { ResourceNotFoundError } from "../errors/api/ResourceNotFoundError";
import { ApiConnectionError } from "../errors/api/ApiConnectionError";
import { UnauthorizedError } from "../errors/api/UnauthorizedError";
import jwt from 'jwt-decode';
import { IApplication, IFaqElement, IJobType, INotification, IVacancy } from '../state/types';
import { mapToApplications } from "../state/applications/Application";
import { mapToJobTypes } from '../state/jobTypes/JobType';
import { mapToVacancies } from '../state/vacancies/Vacancy';
import { mapToFaqElements } from "../state/faqElements/FaqElements";
import { mapToNotifications } from "../state/notifications/FaqElements";
import AsyncStorage from '@react-native-async-storage/async-storage';

const DEFAULT_TOKEN_EXP = 24;
const REFRESH_TOKEN_DELAY_TIME = 10;

const LOGIN_ENDPOINT = '/api/login_check';
const VERIFY_EMAIL_ENDPOINT = '/api/user/verify/email';
const REFRESH_TOKEN_ENDPOINT = '/api/token/refresh';
const APPLICATIONS_ENDPOINT = '/api/job-board/applications/';
const JOB_TYPES_ENDPOINT = '/api/job/types';
const JOB_TYPE_ENDPOINT = '/api/job';
const FAQ_ENDPOINT = '/api/job-board/faq/list';
const MESSAGE_HISTORY_ENDPOINT = '/api/message/history/list';

class ApiClient {

  apiClient: AxiosInstance;
  apiToken: String | null;


  constructor() {
    this.apiToken = '';
    AsyncStorage.getItem(API_TOKEN).then(apiToken => {
      this.apiToken = apiToken;
    })

    this.apiClient = axios.create({
      baseURL: config.API_ENDPOINT,
    });
    
    this.apiClient.interceptors.request.use((config: AxiosRequestConfig) => {
      if (config.url !== REFRESH_TOKEN_ENDPOINT && config.url !== LOGIN_ENDPOINT && config.url !== VERIFY_EMAIL_ENDPOINT) {
        this.onRequest(config);
      }
      return config;
    }, (error) => {
      console.log(error);
      return Promise.reject(error);
    });

    this.apiClient.interceptors.response.use((response: AxiosResponse) => {
      return response;
    }, async (error: any) => {
      if (((error || {}).response || {}).status) {        
        switch (error.response.status) {
          case 401:
            if (error.config && error.config.url !== LOGIN_ENDPOINT) {
              console.log('401',  error.config.url);
              let email = await AsyncStorage.getItem('email');
              let pin = await AsyncStorage.getItem('pin');
              if (email && pin) {
                return this.authenticate(email.trim(), pin).then((apiToken: string) => {
                  error.config.headers.Authorization = 'Bearer ' + apiToken;
                  return this.apiClient.request(error.config);
                }).catch((error: Error) => {
                  this.logout();
                  return Promise.reject(error);
                });
              }
            }
            return Promise.reject(new UnauthorizedError());
          case 404:
            return Promise.reject(new ResourceNotFoundError());
          case 409:
            console.log(error);
            return Promise.reject(new ResourceNotFoundError());
          default: 
            console.log(error.response);
            return Promise.reject(new ApiError('Api error with code: ' + error.response.status))
        }
      } else {
        console.log(error);
        return Promise.reject(new ApiConnectionError())
      } 
    });  
  }

  logout() {
    AsyncStorage.removeItem(API_TOKEN);
    AsyncStorage.removeItem('email');
    AsyncStorage.removeItem('pin');
  }

  onRequest(config: AxiosRequestConfig) {
    if (this.apiToken) {
      let decodedStoredToken = jwt(this.apiToken);
      if (config.url !== REFRESH_TOKEN_ENDPOINT
        && this.isValidDecodedTokenData(decodedStoredToken) 
        && (decodedStoredToken.exp - (DEFAULT_TOKEN_EXP - REFRESH_TOKEN_DELAY_TIME) * 60) < (Date.now() / 1000)) {
          this.refreshToken().catch(error => {
          console.log(error)
        }); 
      }
    }
  }

  isValidDecodedTokenData(token:any) {
    return token && token.exp;
  }

  verifyEmail(email: string): Promise<Object | Error> {
    return new Promise((resolve, reject) => {
      this.apiClient.request({
        method: 'POST',
        url: VERIFY_EMAIL_ENDPOINT,
        data: {
          email
        }
      }).then(async (response: AxiosResponse) => {
        if (response) {
          console.log(response.data);
          resolve(
            response.data
          );
        }
      }).catch(error => {
        console.log(error);
        reject(error);
      })
    }); 
  }

  authenticate(email: string, pin: string): Promise<Object | Error> {
    return new Promise(async (resolve, reject) => {
      let mobileToken = await AsyncStorage.getItem('expoPushToken');
      this.apiClient.request({
        method: 'POST',
        url: LOGIN_ENDPOINT,
        data: {
          username: email,
          password: pin,
          mobileToken
        }
      }).then(async (response: AxiosResponse) => {
        if (response.data.token) {
          this.apiToken = response.data.token;
          await AsyncStorage.setItem(API_TOKEN, response.data.token);
          await AsyncStorage.setItem('email', email);
          await AsyncStorage.setItem('pin', pin);
          resolve(
            response.data.token
          );
        } else {
          reject(new UndefinedApiTokenError());
        }
      }).catch(error => {
        console.log(error)
        reject(error);
      })
    }); 
  }

  refreshToken() {
    return new Promise((resolve, reject) => {
      this.apiClient.get(
        REFRESH_TOKEN_ENDPOINT,
        {
          headers: {
            Authorization: 'Bearer ' + this.apiToken,
          },
        })
        .then(async (response) => {
          if (!((response || {}).data || {}).token) {
            throw new Error('Response does not contain token');
          }
          this.apiToken = response.data.token;
          await AsyncStorage.setItem(API_TOKEN, response.data.token);
          resolve()
        })
        .catch((error: Error) => {
          reject(error);
        }
      );
    })
  }
  
  fetchApplications(): Promise<IApplication[]> {
    return new Promise(async (resolve, reject) => {
      const apiToken = await AsyncStorage.getItem(API_TOKEN);
      this.apiClient.get(
        APPLICATIONS_ENDPOINT,
        {
          headers: {
            Authorization: 'Bearer ' + apiToken,
          },
        })
        .then(function (response: AxiosResponse) {
          resolve(mapToApplications(response.data));
        })
        .catch(function (error) {
          reject(error);
        }
      );
    })
  }

  fetchJobTypes(): Promise<IJobType[]> {
    return new Promise(async (resolve, reject) => {
      const apiToken = await AsyncStorage.getItem(API_TOKEN);
      this.apiClient.get(
          JOB_TYPES_ENDPOINT,
          {
            headers: {
              Authorization: 'Bearer ' + apiToken,
            },
          })
          .then(function (response: AxiosResponse) {
            resolve(mapToJobTypes(response.data));
          })
          .catch(function (error) {
                reject(error);
              }
          );
    })
  }

  fetchVacancies({
    jobTypeId
  }): Promise<IVacancy[]> {
    return new Promise(async (resolve, reject) => {
      const apiToken = await AsyncStorage.getItem(API_TOKEN);
      this.apiClient.get(
          `${JOB_TYPE_ENDPOINT}/${jobTypeId}`
          ,
          {
            headers: {
              Authorization: 'Bearer ' + apiToken,
            },
          })
          .then(function (response: AxiosResponse) {
            resolve(mapToVacancies(response.data));
          })
          .catch(function (error) {
                reject(error);
              }
          );
    });
  }

  fetchFaq(): Promise<IFaqElement[]> {
    return new Promise(async (resolve, reject) => {
      const apiToken = await AsyncStorage.getItem(API_TOKEN);
      this.apiClient.get(
        FAQ_ENDPOINT,
        {
          headers: {
            Authorization: 'Bearer ' + apiToken,
          },
        })
        .then(function (response: AxiosResponse) {
          resolve(mapToFaqElements(response.data));
        })
        .catch(function (error) {
          console.log(error);
          reject(error);
        }
      );
    })
  }

  fetchMessagesHistory({
    page = 1,
    perPage = 20
  }: {
    page: number,
    perPage: number
  }): Promise<INotification[]> {
    return new Promise(async (resolve, reject) => {
      const apiToken = await AsyncStorage.getItem(API_TOKEN);
      console.log(`${MESSAGE_HISTORY_ENDPOINT}/${page}/${perPage}`);
      this.apiClient.get(
        `${MESSAGE_HISTORY_ENDPOINT}/${page}/${perPage}`,
        {
          headers: {
            Authorization: 'Bearer ' + apiToken,
          },
        })
        .then(function (response: AxiosResponse) {
          resolve(mapToNotifications(response.data));
        })
        .catch(function (error) {
          reject(error);
        }
      );
    })
  }

  receiveNotification({
    id
  }: {
    id: number,
  }): Promise<AxiosResponse> {
    return new Promise(async (resolve, reject) => {
      const apiToken = await AsyncStorage.getItem(API_TOKEN);
      this.apiClient.post(
        `/api/message/history/${id}/status/read`,
        {
          headers: {
            Authorization: 'Bearer ' + apiToken,
          },
        })
        .then(function (response: AxiosResponse) {
          resolve(response)
        })
        .catch(function (error) {
          reject(error);
        }
      );
    })
  }

}

let apiClient: ApiClient | undefined = undefined;
const getApiClient = () => apiClient ? apiClient : apiClient = new ApiClient();

export{
  ApiClient,
  getApiClient
} 
